import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    currentCity: '',
    searchHistory: [],
    user: null,
    foodName: '',
  },
  mutations: {
    changeCity(state, val) {
      state.currentCity = val;
    },
    setHistory(state, list) { // 
      state.searchHistory = list;
    },
    getUser(state, value) {
      state.user = value;
    },
    setFoodName(state, val) {
      state.foodName = val;
    },
    changeHistory(state, { type, value }) { // 
      if (type === 'set') {
        // 
        const request = state.searchHistory.filter((item) => item.value === value);
        if (request.length) {
          request[0].time = new Date().getTime();
          state.searchHistory = state.searchHistory.sort((a, b) => b.time - a.time);
        } else {
          const obj = {
            value,
            time: new Date().getTime(),
          };
          state.searchHistory.unshift(obj);
        }
        localStorage.setItem('searchHistory', JSON.stringify(state.searchHistory));
      } else if (type === 'remove') {
        state.searchHistory = [];
        localStorage.removeItem('searchHistory');
      }
    },
  },
  actions: {
    changeCity({ commit }, val) {
      commit('changeCity', val);
    },
    getLocalUser({ commit }) {
      const userInfo = JSON.parse(localStorage.getItem('user'));
      commit('getUser', userInfo);
      return userInfo;
    },
  },
  modules: {
  },
});
